@use "../variables" as *;
@use "../mixins" as *;

.wpmoo-section-fields,
.wpmoo-fieldset__fields {
  display: flex;
  flex-wrap: wrap;
}

.wpmoo-section-fields {
  gap: wpmoo-spacing(xl);
}

.wpmoo-fieldset-group {
  --wpmoo-fieldset-gap: wpmoo-spacing(lg);
  width: 100%;
}

.wpmoo-fieldset--gutter-sm {
  --wpmoo-fieldset-gap: wpmoo-spacing(sm);
}

.wpmoo-fieldset--gutter-md {
  --wpmoo-fieldset-gap: wpmoo-spacing(md);
}

.wpmoo-fieldset--gutter-lg {
  --wpmoo-fieldset-gap: wpmoo-spacing(lg);
}

.wpmoo-fieldset--gutter-xl {
  --wpmoo-fieldset-gap: wpmoo-spacing(xl);
}

.wpmoo-fieldset__fields {
  gap: var(--wpmoo-fieldset-gap);
}

.wpmoo-field {
  --wpmoo-field-width: 100%;

  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: wpmoo-spacing(lg);
  padding: 0;
  margin: 0;
  max-width: 100%;
  background: transparent;
  border: none;
  box-shadow: none;
  flex: 1 1 100%;
}

.wpmoo-field--has-width {
  flex: 0 0 var(--wpmoo-field-width);
  max-width: var(--wpmoo-field-width);
}

.wpmoo-field--separated {
  padding: wpmoo-spacing(lg);
  margin: 0 0 wpmoo-spacing(md);
  border-top: 1px solid wpmoo-color(border-soft);
}



.wpmoo-title {
  display: flex;
  flex-direction: column;
  gap: wpmoo-spacing(xs);
  padding: 0;
  flex: 0 0 240px;
  max-width: 260px;

  h4 {
    margin: 0;
    font-size: 13px;
    font-weight: 600;
    color: wpmoo-color(heading);
    line-height: 1.4;
  }
}

.wpmoo-title__heading {
  display: flex;
  align-items: center;
  gap: wpmoo-spacing(xs);
}

.wpmoo-subtitle-text {
  margin: 0;
  font-size: 12px;
  line-height: 1.6;
  color: wpmoo-color(text);
}

.wpmoo-fieldset,
.wpmoo-fieldset__body {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  gap: wpmoo-spacing(xs);

  input[type="text"],
  input[type="email"],
  input[type="url"],
  input[type="number"],
  input[type="date"],
  input[type="time"],
  textarea,
  select {
    width: 100%;
    padding: wpmoo-spacing(sm) wpmoo-spacing(md);
    border: 1px solid wpmoo-color(border);
    border-radius: wpmoo-radius(field);
    font-size: 13px;
    line-height: 1.4;
    background: wpmoo-color(field-background);
    color: wpmoo-color(text);
    @include transition(border-color, box-shadow);

    // Placeholder color (plus vendor fallbacks)
    &::placeholder {
      color: wpmoo-color(text-muted);
      opacity: 1;
    }

    &::-webkit-input-placeholder {
      color: wpmoo-color(text-muted);
      opacity: 1;
    }

    &::-moz-placeholder {
      color: wpmoo-color(text-muted);
      opacity: 1;
    }

    &:-ms-input-placeholder {
      color: wpmoo-color(text-muted);
    }

    &::-ms-input-placeholder {
      color: wpmoo-color(text-muted);
    }

    &:disabled {
      background: wpmoo-color(field-background-disabled);
      cursor: not-allowed;
      color: wpmoo-color(text-muted);
    }
  }

  input:focus,
  textarea:focus,
  select:focus {
    @include focus-ring;
  }

  textarea {
    min-height: 120px;
    resize: vertical;
  }

  // Optional monospace helper for code-like inputs
  // Matches examples using ->attributes(['class' => 'monospace']) in fields
  textarea.monospace,
  .monospace.wp-editor-area,
  input.monospace,
  .wpmoo-input__control.monospace {
    font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    font-size: 12px;
  }

  input[type="checkbox"] {
    margin-right: wpmoo-spacing(sm);
  }

  input[type="color"] {
    width: 60px;
    height: 34px;
    padding: 2px;
    border-radius: wpmoo-radius(field);
    border: 1px solid wpmoo-color(border);
    background: #fff;
  }
}

.wpmoo-field--nested {
  padding: 0 0 wpmoo-spacing(md);
  margin: 0;
  border-bottom: 1px solid wpmoo-color(border-soft);
  flex-direction: column;
  gap: wpmoo-spacing(sm);
}

.wpmoo-field--nested:last-child {
  border-bottom: none;
  padding-bottom: 0;
}

.wpmoo-field--nested .wpmoo-title h4 {
  font-size: 12px;
}

.wpmoo-field--nested .wpmoo-fieldset__body {
  padding-bottom: 0;
}

.wpmoo-fieldset__control {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.wpmoo-fieldset__control>* {
  width: 100%;
}

.wpmoo-fieldset__control-inner {
  display: flex;
  align-items: center;
  gap: wpmoo-spacing(xs);
  width: 100%;

  > :first-child {
    min-width: 0;
  }

  .wpmoo-field-help {
    flex: 0 0 auto;
  }
}

// Input with icon wrapper (used by fields supporting an inline icon, e.g., Text)
.wpmoo-input {
  position: relative;
  display: inline-flex;
  align-items: center;
  width: 100%;

  &__control {
    width: 100%;
  }

  &__icon {
    position: absolute;
    left: 10px; // fallback for LTR
    inset-inline-start: 10px; // RTL-aware
    color: wpmoo-color(text-muted);
    pointer-events: none;
    line-height: 1;
  }

  &--icon-right {
    .wpmoo-input__icon {
      left: auto;
      right: 10px; // fallback for LTR
      inset-inline-start: auto; // RTL-aware
      inset-inline-end: 10px; // RTL-aware
    }

    .wpmoo-input__control {
      padding-right: 2rem;
    }
  }

  &--icon-left {
    .wpmoo-input__control {
      padding-left: 2rem;
    }
  }
}

// Repeatable wrapper
.wpmoo-repeat {
  display: flex;
  flex-direction: column;
  gap: wpmoo-spacing(sm);

  &__items {
    display: flex;
    flex-direction: column;
    gap: wpmoo-spacing(xs);
  }

  &__item {
    background: #fff;
    border: 1px solid wpmoo-color(border-soft);
    border-radius: wpmoo-radius(field);
    box-shadow: 0 1px 0 rgba(0, 0, 0, .03);
    overflow: hidden;
    position: relative; // helpful for sortable helper/placeholder
    // Create two columns: main content and a narrow side control rail
    display: grid;
    grid-template-columns: 1fr 56px;
  }

  &__main {
    grid-column: 1;
    min-width: 0; // prevent overflow from nested fields
  }

  &__side {
    grid-column: 2;
    display: flex;
    flex-direction: column;
    gap: 6px;
    align-items: center;
    padding: 8px 6px;
    background: wpmoo-color(panel-bg);
    border-left: 1px solid wpmoo-color(border-soft);

    .button {
      min-width: auto;
      width: 100%;
      height: 28px;
      padding: 2px 0;
      border: none;
      background: transparent;
      box-shadow: none;
      color: wpmoo-color(primary);
    }

    .button:disabled {
      opacity: .45;
      cursor: default;
    }
  }

  &__head {
    display: grid;
    grid-template-columns: 28px 1fr auto;
    align-items: center;
    gap: wpmoo-spacing(xs);
    padding: wpmoo-spacing(sm) wpmoo-spacing(md);
    background: wpmoo-color(panel-bg);
    border-bottom: 1px solid wpmoo-color(border-soft);
  }

  &__title {
    font-weight: 600;
    color: wpmoo-color(heading);
    flex: 1 1 auto;
  }

  &__actions {
    display: inline-flex;
    gap: 2px;

    .button {
      min-width: auto;
      padding: 2px 6px;
      height: 28px;
      border: none;
      background: transparent;
      box-shadow: none;
      color: wpmoo-color(primary);
    }

    .button:disabled {
      opacity: .45;
      cursor: default;
    }
  }

  &__handle {
    cursor: move;
    min-width: 28px;
    height: 28px;
    border: none;
    border-color: transparent;
    background: transparent;
    box-shadow: none;
    color: wpmoo-color(toggle-indicator);
    padding: 2px 6px;
  }

  // Ensure WP button styles don't force borders on our head buttons
  &__head .button.wpmoo-repeat__handle {
    border: none;
    border-color: transparent;
    background: transparent;
    box-shadow: none;
  }

  &__body {
    padding: wpmoo-spacing(sm) wpmoo-spacing(md);
  }

  &__add {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    background: wpmoo-color(panel-bg);
    border: 1px solid wpmoo-color(border);
    border-radius: wpmoo-radius(field);
    padding: wpmoo-spacing(md);
    color: wpmoo-color(primary);
    box-shadow: none;
  }
}

.wpmoo-fieldset__control-inner textarea+.wpmoo-field-help,
.wpmoo-fieldset__control-inner .wp-editor-area+.wpmoo-field-help {
  align-self: flex-start;
  margin-top: wpmoo-spacing(xs);
}

.wpmoo-fieldset__control-inner input[type="color"],
.wpmoo-fieldset__control-inner .wp-color-picker,
.wpmoo-fieldset__control-inner .wp-color-result {
  flex: 0 0 auto;
  width: auto;
  min-width: 0;
}

.wpmoo-field-fieldset {
  padding: wpmoo-spacing(lg) 0;
  margin-bottom: wpmoo-spacing(xl);
  background: transparent;
  border: none;
  box-shadow: none;
}

.wpmoo-field-fieldset:last-child {
  margin-bottom: 0;
}

.wpmoo-fieldset__body {
  display: flex;
  flex-direction: column;
  gap: wpmoo-spacing(xs);
}

.wpmoo-field-before,
.wpmoo-field-after {
  margin: 0 0 wpmoo-spacing(xs);
  color: wpmoo-color(text);
  font-size: 12px;
  line-height: 1.5;
}

.wpmoo-field-help {
  --wpmoo-help-size: 1.125rem;
  --wpmoo-help-gap: wpmoo-spacing(sm);

  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: var(--wpmoo-help-size);
  height: var(--wpmoo-help-size);
  padding: 0;
  border: none;
  border-radius: 999px;
  background: rgba(29, 35, 39, 0.08);
  color: wpmoo-color(text);
  font-size: 0.75rem;
  font-weight: 600;
  line-height: 1;
  cursor: pointer;
  flex-shrink: 0;
  text-decoration: none;
  transition: background-color var(--wpmoo-transition-duration, 0.2s) var(--wpmoo-transition-timing, ease),
    color var(--wpmoo-transition-duration, 0.2s) var(--wpmoo-transition-timing, ease),
    box-shadow var(--wpmoo-transition-duration, 0.2s) var(--wpmoo-transition-timing, ease);

  span[aria-hidden="true"] {
    display: inline-block;
    transform: translateY(-1px);
  }

  &:hover,
  &:focus-visible {
    background: rgba(29, 35, 39, 0.16);
    color: wpmoo-color(heading);
  }

  &:focus-visible {
    @include focus-ring;
  }
}

.wpmoo-field-help.is-active {
  background: rgba(29, 35, 39, 0.16);
  color: wpmoo-color(heading);
}

.wpmoo-help-popover {
  position: absolute;
  z-index: 10000;
  min-width: 200px;
  max-width: min(320px, calc(100vw - 48px));
  background: #fff;
  color: wpmoo-color(heading);
  border-radius: wpmoo-radius(field);
  border: 1px solid wpmoo-color(border-soft);
  box-shadow: 0 18px 40px rgba(15, 23, 42, 0.18);
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--wpmoo-transition-duration, 0.2s) var(--wpmoo-transition-timing, ease),
    visibility var(--wpmoo-transition-duration, 0.2s) var(--wpmoo-transition-timing, ease),
    transform var(--wpmoo-transition-duration, 0.2s) var(--wpmoo-transition-timing, ease);

  &.is-visible {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  &[data-side="right"] {
    transform-origin: left center;
  }

  &[data-side="left"] {
    transform-origin: right center;
  }
}

.wpmoo-help-popover__content {
  padding: wpmoo-spacing(sm) wpmoo-spacing(md);
  font-size: 13px;
  line-height: 1.5;

  p {
    margin: 0 0 wpmoo-spacing(xs);

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    color: wpmoo-color(primary);
    text-decoration: underline;

    &:hover,
    &:focus {
      text-decoration: none;
    }
  }
}

.wpmoo-help-popover__arrow {
  position: absolute;
  width: 14px;
  height: 14px;
  background: #fff;
  border: 1px solid wpmoo-color(border-soft);
  transform: rotate(45deg);
  z-index: 0;
}

.wpmoo-help-popover[data-side="right"] .wpmoo-help-popover__arrow {
  left: -7px;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  border-right: none;
  border-top: none;
}

.wpmoo-help-popover[data-side="left"] .wpmoo-help-popover__arrow {
  right: -7px;
  top: 50%;
  transform: translateY(-50%) rotate(45deg);
  border-left: none;
  border-bottom: none;
}

.wpmoo-field-help-text {
  margin: wpmoo-spacing(xs) 0 0;
  color: wpmoo-color(text);
  font-size: 12px;
  line-height: 1.5;
}

@media (max-width: 960px) {
  .wpmoo-field {
    flex-direction: column;
    gap: wpmoo-spacing(md);
  }

  .wpmoo-title {
    flex: 0 0 auto;
    max-width: 100%;
    width: 100%;
  }

  .wpmoo-title__heading {
    justify-content: space-between;
  }

  .wpmoo-help-popover[data-side="right"],
  .wpmoo-help-popover[data-side="left"] {
    transform: translateY(6px);
  }
}

@media (max-width: 782px) {
  .wpmoo-field--has-width {
    flex-basis: 100%;
    max-width: 100%;
  }

  .wpmoo-section-fields,
  .wpmoo-fieldset__fields {
    gap: wpmoo-spacing(lg);
  }
}
